// src/components/game/actions/BaseActionButtons.js
import { useState } from 'react';
import { useGame } from '../../../context/GameContext';
import ActionButton from './ActionButton';

const BaseActionButtons = () => {
  const { 
    state, 
    log, 
    updateDungeon,
    updateCharacter,
    nextDungeonLevel,
    spawnEnemy,
    checkSpecialEvent
  } = useGame();
  
  const [isLoading, setIsLoading] = useState(false);
  
  const { dungeon, character } = state;
  
  // /
  const canExplore = !dungeon.enemyPresent && (!dungeon.treasurePresent || dungeon.treasureOpened) && !dungeon.trapPresent && !isLoading;
  const canRest = !dungeon.enemyPresent && (!dungeon.treasurePresent || dungeon.treasureOpened) && !dungeon.trapPresent && !isLoading;
  const canGoNextLevel = dungeon.clearedRooms >= dungeon.roomsToNextLevel && !dungeon.enemyPresent && (!dungeon.treasurePresent || dungeon.treasureOpened) && !dungeon.trapPresent && !isLoading;
  
  // Handle exploration
  const handleExplore = () => {
    setIsLoading(true);
    
    // Add a delay to simulate action
    setTimeout(() => {
      // 
      updateDungeon({
        treasurePresent: false,
        treasureOpened: false,
        trapPresent: false,
        currentTrap: null
      });
      
      // Check for special events first
      if (checkSpecialEvent()) {
        setIsLoading(false);
        return;
      }
      
      // Check for traps
      const trapCheck = Math.random() < 0.2;
      if (trapCheck) {
        handleTrapEncounter();
      } else {
        // No trap, determine room contents
        handleRoomContents();
      }

      setIsLoading(false);
    }, 500);
  };
  
  // Handle trap encounter
  const handleTrapEncounter = () => {
    const trapIndex = Math.floor(Math.random() * 3);
    const trapTypes = [
      { 
        name: "", 
        difficulty: 8 + dungeon.level, 
        damage: 3 + dungeon.level,
        description: ""
      },
      { 
        name: "", 
        difficulty: 10 + dungeon.level, 
        damage: 5 + dungeon.level,
        description: ""
      },
      { 
        name: "", 
        difficulty: 12 + dungeon.level, 
        damage: 8 + dungeon.level,
        description: ""
      }
    ];
    
    const trap = trapTypes[trapIndex];
    log(`${trap.name}`, 'failure');
    
    // TrapDisplay
    updateDungeon({ 
      trapPresent: true,
      currentTrap: trap
    });
  };
  
  // Handle room contents discovery
  const handleRoomContents = () => {
    const roomType = Math.random();
    
    // 
    if (dungeon.isBossFloor) {
      const monster = spawnEnemy(true); // 
      log(`...${monster.name}`, 'danger');
      return;
    }
    
    if (roomType < 0.6) {  // 60% chance for enemy
      const monster = spawnEnemy();
      log(`${monster.name}`);
    } else if (roomType < 0.9) {  // 30% chance for treasure
      updateDungeon({ treasurePresent: true });
      log("", 'success');
    } else {  // 10% chance for empty room
      log("");
      updateDungeon({ 
        clearedRooms: dungeon.clearedRooms + 1 
      });
    }

  };
  
  // Handle rest
  const handleRest = () => {
    setIsLoading(true);
    
    setTimeout(() => {
      // 
      updateDungeon({
        treasurePresent: false,
        treasureOpened: false,
        trapPresent: false,
        currentTrap: null
      });
      
      const hpRecover = Math.floor(character.maxHp * 0.2);
      const mpRecover = Math.floor(character.maxMp * 0.2);
      
      const newHp = Math.min(character.hp + hpRecover, character.maxHp);
      const newMp = Math.min(character.mp + mpRecover, character.maxMp);
      
      updateCharacter({
        hp: newHp,
        mp: newMp
      });
      
      log(`HP${hpRecover}MP${mpRecover}`, 'success');
      
      // Chance of being ambushed while resting
      if (Math.random() < 0.3) {
        const monster = spawnEnemy();
        log(`${monster.name}`, 'failure');
      }
      
      setIsLoading(false);
    }, 500);
  };
  
  // Handle next dungeon level
  const handleNextLevel = () => {
    // 
    if (dungeon.isBossFloor) {
      log("...", 'warning');
      return;
    }
    
    setIsLoading(true);
    
    setTimeout(() => {
      nextDungeonLevel();
      setIsLoading(false);
    }, 500);
  };
  
  return (
    <div className="grid grid-cols-3 gap-2">
      <ActionButton
        action="explore"
        label=""
        onClick={handleExplore}
        disabled={!canExplore}
      />
      
      <ActionButton
        action="rest"
        label=""
        onClick={handleRest}
        disabled={!canRest}
      />
      
      <ActionButton
        action="next"
        label=""
        onClick={handleNextLevel}
        disabled={!canGoNextLevel}
      />
    </div>
  );
};

export default BaseActionButtons;
