const itemDatabase = {
  weapons: [
    { id: 'basicSword', name: '', attack: 3, weight: 1, price: 50, description: '' },
    { id: 'ironSword', name: '', attack: 5, weight: 2, price: 120, description: '' },
    { id: 'silverSword', name: '', attack: 8, weight: 1, price: 300, description: '' },
    { id: 'magicSword', name: '', attack: 12, weight: 1, price: 600, description: '' },
    { id: 'chiefSword', name: '', attack: 7, weight: 2, price: 0, description: '' }
  ],
  armours: [
    { id: 'clothArmour', name: '', defense: 2, weight: 0, price: 40, description: '' },
    { id: 'leatherArmour', name: '', defense: 4, weight: 1, price: 100, description: '' },
    { id: 'chainMail', name: '', defense: 7, weight: 3, price: 250, description: '' },
    { id: 'plateMail', name: '', defense: 10, weight: 5, price: 500, description: '' },
    { id: 'guardianShield', name: '', defense: 8, weight: 2, price: 0, description: '' }
  ],
  accessories: [
    { id: 'warriorRing', name: '', effect: { stats: { str: 1 } }, price: 0, description: '' },
    { id: 'ancientTalisman', name: '', effect: { stats: { int: 2, mnd: 1 } }, price: 0, description: '' }
  ]
};

/**
 * ID
 * @param {string} itemId - ID
 * @returns {Object|null} - null
 */
export const findItemById = (itemId) => {
  for (const category of Object.keys(itemDatabase)) {
    const item = itemDatabase[category].find(item => item.id === itemId);
    if (item) return item;
  }
  return null;
};

export default itemDatabase; 
