// src/components/quests/QuestStatusBar.js
import React from 'react';
import { useGame } from '../../context/GameContext';
import { QUEST_DATABASE } from '../../data/quests';

/**
 * 
 * TownPageGamePage
 */
const QuestStatusBar = () => {
  const { state } = useGame();
  const { questJournal } = state;
  
  // ID
  if (!questJournal?.trackedQuestId) {
    return null;
  }
  
  // 
  const trackedQuestId = questJournal.trackedQuestId;
  const activeQuest = questJournal.activeQuests?.find(q => q.id === trackedQuestId);
  
  // 
  if (!activeQuest) {
    return null;
  }
  
  // 
  const questData = QUEST_DATABASE.find(q => q.id === trackedQuestId);
  if (!questData) {
    return null;
  }
  
  // 
  const completedObjectives = activeQuest.objectives.filter(obj => obj.isCompleted).length;
  const totalObjectives = activeQuest.objectives.length;
  const progressPercent = Math.round((completedObjectives / totalObjectives) * 100);
  
  return (
    <div className="bg-indigo-800 rounded-lg p-3 mb-4 shadow-sm">
      <div className="flex justify-between items-center mb-2">
        <div className="flex items-center">
          <div className="bg-blue-600 rounded-full w-2 h-2 mr-2"></div>
          <h4 className="text-sm font-medium text-sky-300">: {questData.title}</h4>
        </div>
        <div className="text-xs text-indigo-300">
          : {completedObjectives}/{totalObjectives}
        </div>
      </div>
      
      {/*  */}
      <div className="w-full bg-indigo-900 rounded-full h-1.5 mb-1">
        <div 
          className="bg-blue-600 h-1.5 rounded-full transition-all duration-300"
          style={{ width: `${progressPercent}%` }}
        ></div>
      </div>
      
      {/*  */}
      {activeQuest.objectives.some(obj => !obj.isCompleted) && (
        <div className="text-xs text-indigo-200 mt-1">
          : {
            questData.objectives.find((_, idx) => !activeQuest.objectives[idx].isCompleted)?.description || ''
          }
        </div>
      )}
    </div>
  );
};

export default QuestStatusBar;
