// src/context/reducers/equipmentReducer.js
import { ACTION_TYPES } from '../actions';

/**
 * 
 * @param {Object} state - 
 * @param {Object} action - 
 * @returns {Object} 
 */
export const equipmentReducer = (state, action) => {
  switch (action.type) {
    case ACTION_TYPES.EQUIP_ITEM: {
      const { slot, item } = action.payload;
      
      // 
      const currentEquipment = state.equipment[slot];
      
      // 
      const newEquipment = {
        ...state.equipment,
        [slot]: item
      };
      
      // 
      let newInventory = state.inventory.filter(invItem => invItem.id !== item.id);
      
      // 
      if (currentEquipment) {
        newInventory = [...newInventory, currentEquipment];
      }
      
      return {
        ...state,
        equipment: newEquipment,
        inventory: newInventory
      };
    }
    
    case ACTION_TYPES.UNEQUIP_ITEM: {
      const slot = action.payload;
      const currentEquipment = state.equipment[slot];
      
      // 
      if (!currentEquipment) {
        return state;
      }
      
      // 
      return {
        ...state,
        equipment: {
          ...state.equipment,
          [slot]: null
        },
        inventory: [...state.inventory, currentEquipment]
      };
    }
    
    case ACTION_TYPES.ADD_TO_INVENTORY: {
      const newItem = action.payload;
      
      // ID
      if (state.inventory.some(item => item.id === newItem.id)) {
        return state;
      }
      
      return {
        ...state,
        inventory: [...state.inventory, newItem]
      };
    }
    
    case ACTION_TYPES.REMOVE_FROM_INVENTORY: {
      const itemId = action.payload;
      
      return {
        ...state,
        inventory: state.inventory.filter(item => item.id !== itemId)
      };
    }
    
    case ACTION_TYPES.BUY_ITEM: {
      const item = action.payload;
      const cost = item.price;
      
      // 
      if (state.gold < cost) {
        return state;
      }
      
      return {
        ...state,
        gold: state.gold - cost,
        inventory: [...state.inventory, item]
      };
    }
    
    case ACTION_TYPES.SELL_ITEM: {
      const item = action.payload;
      const sellPrice = Math.floor(item.price / 2); // 
      
      return {
        ...state,
        gold: state.gold + sellPrice,
        inventory: state.inventory.filter(invItem => invItem.id !== item.id)
      };
    }
    
    default:
      return state;
  }
};

export default equipmentReducer;
