/**
 * 
 * @param {string} characterId - ID
 * @param {string} conversationId - ID
 * @returns {Object|null} null
 */
export const getConversationEvent = (characterId, conversationId) => {
  return CONVERSATION_EVENTS.find(
    event => event.characterId === characterId && event.conversationId === conversationId
  ) || null;
}; 
