// src/components/game/TreasureDisplay.js
import { useState } from 'react';
import { useGame } from '../../context/GameContext';

const TreasureDisplay = () => {
  const { state, openTreasure, log, gainXP, checkRoll, updateCharacter, gameOver } = useGame();
  const { dungeon, character } = state;
  
  // 
  const [isChecking, setIsChecking] = useState(false);
  const [trapChecked, setTrapChecked] = useState(false);
  const [hasTrap, setHasTrap] = useState(false);
  const [trapDisarmed, setTrapDisarmed] = useState(false);
  
  // 
  const treasureOpened = dungeon.treasureOpened;
  
  // 
  const [trap] = useState(() => {
    //  ()
    const trapChance = 0.5 + (dungeon.level * 0.05);
    
    if (Math.random() < trapChance) {
      // 
      const trapTypes = [
        { 
          name: "", 
          difficulty: 8 + dungeon.level, 
          damage: 4 + dungeon.level,
          description: ""
        },
        { 
          name: "", 
          difficulty: 10 + dungeon.level, 
          damage: 6 + dungeon.level,
          description: ""
        },
        { 
          name: "", 
          difficulty: 12 + dungeon.level, 
          damage: 8 + dungeon.level,
          description: ""
        }
      ];
      return trapTypes[Math.floor(Math.random() * trapTypes.length)];
    }
    
    return null;
  });
  
  // 
  const handleCheckTrap = () => {
    setIsChecking(true);
    
    setTimeout(() => {
      // 
      const searchResult = checkRoll(
        character.stats.dex,
        character.skills.search,
        trap ? 8 : 5 // 
      );
      
      setTrapChecked(true);
      
      if (trap) {
        if (searchResult.success) {
          // 
          setHasTrap(true);
          log(`${trap.name}`, 'success');
        } else {
          // 
          log(`...`, 'normal');
        }
      } else {
        // 
        log(``, 'success');
      }
      
      setIsChecking(false);
    }, 500);
  };
  
  // 
  const handleDisarmTrap = () => {
    setIsChecking(true);
    
    setTimeout(() => {
      // 
      const disarmResult = checkRoll(
        character.stats.dex,
        character.skills.trap,
        trap.difficulty
      );
      
      if (disarmResult.success) {
        // 
        setTrapDisarmed(true);
        log(`${trap.name}`, 'success');
        
        // 
        const xpBonus = 3 + Math.floor(dungeon.level / 2);
        log(`${xpBonus}`, 'success');
        
        if (gainXP) {
          gainXP(xpBonus);
        }
      } else {
        //  - 
        log(`${trap.name}`, 'failure');
        handleTrapEffect();
      }
      
      setIsChecking(false);
    }, 500);
  };
  
  // 
  const handleRiskyOpen = () => {
    setIsChecking(true);
    
    setTimeout(() => {
      // 
      const evadeResult = checkRoll(
        character.stats.dex,
        character.skills.trap,
        trap.difficulty - 2 // 
      );
      
      if (evadeResult.success) {
        //  - 
        log(`${trap.name}`, 'success');
        handleOpenTreasure();
      } else {
        //  - 
        log(`${trap.name}`, 'failure');
        handleTrapEffect();
        
        // 
        handleOpenTreasure();
      }
      
      setIsChecking(false);
    }, 500);
  };
  
  // 
  const handleTrapEffect = () => {
    if (!trap) return;
    
    const damage = trap.damage;
    const newHp = Math.max(0, character.hp - damage);
    
    updateCharacter({ hp: newHp });
    
    log(`${trap.name}${damage}`, 'failure');
    
    // Check for game over
    if (newHp <= 0) {
      gameOver();
    }
  };
  
  // 
  const handleOpenTreasure = () => {
    // 
    const treasure = openTreasure();
    
    // 
    if (treasure) {
      log(`${treasure.name}${treasure.effect}`, 'critical');
    } else {
      log(``, 'success');
    }
  };
  
  // 
  const handleSafeOpen = () => {
    setIsChecking(true);
    
    setTimeout(() => {
      handleOpenTreasure();
      setIsChecking(false);
    }, 500);
  };
  
  // 
  const renderContent = () => {
    if (treasureOpened) {
      // 
      return (
        <p className="text-amber-100">
          
        </p>
      );
    } else if (!trapChecked) {
      // 
      return (
        <div>
          <p className="mb-4 text-amber-100">
            
          </p>
          <div className="flex flex-col sm:flex-row justify-center space-y-2 sm:space-y-0 sm:space-x-3">
            <button
              onClick={handleCheckTrap}
              disabled={isChecking}
              className={`bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${isChecking ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              
            </button>
            <button
              onClick={handleOpenTreasure}
              disabled={isChecking}
              className={`bg-amber-600 hover:bg-amber-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${isChecking ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              
            </button>
          </div>
        </div>
      );
    } else if (hasTrap && !trapDisarmed) {
      // 
      return (
        <div>
          <p className="mb-2 text-amber-100">
            <span className="text-red-300 font-semibold">{trap.name}</span>
          </p>
          <p className="mb-4 text-amber-100 text-sm">
            {trap.description}
          </p>
          <div className="flex flex-col sm:flex-row justify-center space-y-2 sm:space-y-0 sm:space-x-3">
            <button
              onClick={handleDisarmTrap}
              disabled={isChecking}
              className={`bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${isChecking ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              
            </button>
            <button
              onClick={handleRiskyOpen}
              disabled={isChecking}
              className={`bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${isChecking ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              
            </button>
          </div>
        </div>
      );
    } else {
      // 
      return (
        <div>
          <p className="mb-4 text-amber-100">
            {trapDisarmed 
              ? "" 
              : ""}
          </p>
          <button
            onClick={handleSafeOpen}
            disabled={isChecking}
            className={`bg-amber-600 hover:bg-amber-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${isChecking ? 'opacity-50 cursor-not-allowed' : ''}`}
          >
            
          </button>
        </div>
      );
    }
  };
  
  return (
    <div className="bg-amber-900/40 rounded-lg p-4 text-center py-8">
      <h3 className="text-xl font-bold text-amber-300 mb-4">
        
      </h3>
      
      {renderContent()}
    </div>
  );
};

export default TreasureDisplay;
