// src/utils/SaveLoadSystem.js
/**
 * /
 */

const SAVE_KEY = 'dungeonHackSaveData';

/**
 * 
 * @param {Object} gameState - 
 */
export const saveGame = (gameState) => {
  try {
    // 
    const stateToSave = {
      character: gameState.character,
      dungeon: gameState.dungeon,
      town: gameState.town,
      isGameOver: gameState.isGameOver,
      hasCreatedCharacter: gameState.hasCreatedCharacter,
      // 
      currentContext: window.location.pathname.includes('/game') ? 'dungeon' : 
                      window.location.pathname.includes('/town') ? 'town' :
                      window.location.pathname.includes('/dungeon-select') ? 'dungeon-select' : 'town',
      questJournal: gameState.questJournal
      // gameLog
      // isLevelingUp
      // specialEvent
    };
    
    // 
    stateToSave.savedAt = new Date().toISOString();
    
    // JSON
    const saveData = JSON.stringify(stateToSave);
    localStorage.setItem(SAVE_KEY, saveData);
    
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

/**
 * 
 * @returns {Object|null} null
 */
export const loadGame = () => {
  try {
    const saveData = localStorage.getItem(SAVE_KEY);
    
    if (!saveData) {
      return null;
    }
    
    return JSON.parse(saveData);
  } catch (error) {
    console.error(':', error);
    return null;
  }
};

/**
 * 
 * @returns {boolean} true
 */
export const hasSavedGame = () => {
  return localStorage.getItem(SAVE_KEY) !== null;
};

/**
 * 
 */
export const deleteSavedGame = () => {
  localStorage.removeItem(SAVE_KEY);
};

/**
 * 
 * @returns {string|null} null
 */
export const getSaveDateTime = () => {
  try {
    const saveData = localStorage.getItem(SAVE_KEY);
    
    if (!saveData) {
      return null;
    }
    
    const parsedData = JSON.parse(saveData);
    return parsedData.savedAt;
  } catch (error) {
    console.error(':', error);
    return null;
  }
};

/**
 * 
 * @returns {string|null} 'dungeon', 'town', 'dungeon-select' null
 */
export const getSavedGameContext = () => {
  try {
    const saveData = localStorage.getItem(SAVE_KEY);
    
    if (!saveData) {
      return null;
    }
    
    const parsedData = JSON.parse(saveData);
    return parsedData.currentContext || 'town'; // 
  } catch (error) {
    console.error(':', error);
    return null;
  }
};
