// src/components/game/ActionButtons.js
import { useGame } from '../../context/GameContext';
import BaseActionButtons from './actions/BaseActionButtons';
import CombatActionButtons from './actions/CombatActionButtons';

const ActionButtons = () => {
  const { state } = useGame();
  const { dungeon } = state;
  
  return (
    <div className="mb-6 space-y-3">
      {/*  */}
      {dungeon.enemyPresent && <CombatActionButtons />}
      
      {/*  */}
      <BaseActionButtons />
    </div>
  );
};

export default ActionButtons;
