// src/utils/uiHelpers.js

/**
 * 
 * @param {number} level - 
 * @returns {string} 
 */
export const heartEmoji = (level) => {
  if (level <= 0) return '';
  if (level === 1) return '';
  if (level === 2) return '';
  if (level === 3) return '';
  return '';
};

/**
 * 
 * @param {number} level - (1-5)
 * @returns {string} 
 */
export const difficultyStars = (level) => {
  const filledStars = ''.repeat(Math.min(5, Math.max(0, level)));
  const emptyStars = ''.repeat(Math.max(0, Math.min(5, 5 - level)));
  return filledStars + emptyStars;
};

/**
 * 
 * @param {string} location - 
 * @returns {string} 
 */
export const getLocationName = (location) => {
  switch (location) {
    case 'square': return '';
    case 'tavern': return '';
    case 'weaponShop': return '';
    case 'armourShop': return '';
    default: return '';
  }
};

/**
 * 
 * @param {number} amount - 
 * @returns {string} 
 */
export const formatCurrency = (amount) => {
  return `${amount.toLocaleString()} G`;
};

/**
 * 
 * @param {string} stat - 
 * @returns {string} 
 */
export const getStatName = (stat) => {
  switch (stat) {
    case 'str': return '';
    case 'dex': return '';
    case 'int': return '';
    case 'mnd': return '';
    default: return stat;
  }
};

/**
 * 
 * @param {string} skill - 
 * @returns {string} 
 */
export const getSkillName = (skill) => {
  switch (skill) {
    case 'sword': return '';
    case 'magic': return '';
    case 'trap': return '';
    case 'search': return '';
    case 'negotiation': return '';
    default: return skill;
  }
};

/**
 * 
 * @param {string} type -  ('success', 'error', 'warning', 'info')
 * @returns {Object} 
 */
export const getDialogStyles = (type) => {
  switch (type) {
    case 'success':
      return { bg: 'bg-green-800', text: 'text-green-100' };
    case 'error':
      return { bg: 'bg-red-800', text: 'text-red-100' };
    case 'warning':
      return { bg: 'bg-amber-800', text: 'text-amber-100' };
    case 'info':
    default:
      return { bg: 'bg-indigo-800', text: 'text-indigo-100' };
  }
};

/**
 * 
 * @param {string} name - 
 * @param {string} quality -  ('common', 'uncommon', 'rare', 'epic', 'legendary')
 * @returns {string} 
 */
export const getQualityItemName = (name, quality) => {
  switch (quality) {
    case 'uncommon': return `${name}`;
    case 'rare': return `${name}`;
    case 'epic': return `${name}`;
    case 'legendary': return `${name}`;
    case 'common':
    default:
      return name;
  }
};

/**
 * 
 * @param {string} quality -  ('common', 'uncommon', 'rare', 'epic', 'legendary')
 * @returns {string} Tailwind
 */
export const getQualityTextColor = (quality) => {
  switch (quality) {
    case 'uncommon': return 'text-green-300';
    case 'rare': return 'text-blue-300';
    case 'epic': return 'text-purple-300';
    case 'legendary': return 'text-yellow-300';
    case 'common':
    default:
      return 'text-white';
  }
};
